.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Todo-App {
  padding-top: 35px;
  display: inline-block;
  text-align: left;
}

input {
  font-size: 20px;
}

ul {
  padding: 0px;
}

li {
  list-style-type: none;
}

.message {
  background-color: #61DAFB;
  color: white;
  border: solid 1px #61DAFB;
  position: absolute;
  text-align: center;
  width: 200px;
  left: 50%;
  margin-left: -100px;
  top: 200px;
}

li span.delete-item {
  visibility: hidden;
}

span.delete-item button {
  background: none;
  border: none;
  font-size: 1em;
  font-weight: bold;
  text-decoration: none;
  color: red;
}

li:hover span.delete-item {
  visibility: visible;
}

a {
  padding-left: 3px;
  padding-right: 3px;
}


